
import  'bootstrap/dist/css/bootstrap.min.css';
import {Container} from 'react-bootstrap'
import './App.css';
import {useState} from "react"
import ChangeButton from "./Component/ChangeButton"

function App() {

const [count,setCount] = useState(0)

const increment = () => {
  setCount(count + 1)
}
const decrement = () => {
  if(count === 0){
    setCount(0)
  }else{
    setCount(count-1)
  }
}
const reset = () => {
  setCount(0)
}

  return (
    <>
    <Container>
    <div className="App" >
      <h1>React Counter</h1>
      <h2>{count}</h2>
      <ChangeButton increment={increment} decrement={decrement} reset={reset}/>
    </div>
    </Container>
    </>
  );
}

export default App;
